import React from 'react'
import Form from 'react-bootstrap/Form'
import Button from 'react-bootstrap/Button'
import axios from "axios";



const encode = (data) => {
  return Object.keys(data)
    .map(key => encodeURIComponent(key) + '=' + encodeURIComponent(data[key]))
    .join('&')
}

const Contactform = () => {
  const [serverState, setServerState] = React.useState({
    submitting: false,
    status: null
  });
  const handleServerResponse = (ok, msg, form) => {
    setServerState({
      submitting: false,
      status: { ok, msg }
    });
    if (ok) {
      form.reset();
    }
  };
  const handleOnSubmit = e => {
    e.preventDefault();
    const form = e.target;
    setServerState({ submitting: true });
    axios({
      method: "post",
      url: "https://getform.io/f/2385bd8d-e1db-4320-a546-55b77024cc88",
      data: new FormData(form)
    })
      .then(r => {
        handleServerResponse(true, "Thanks!", form);
      })
      .catch(r => {
        handleServerResponse(false, r.response.data.error, form);
      });
  };
  return(  
    <form onSubmit={handleOnSubmit}>
        <div className="label">Name</div>
        <div className = "control">
        <input className="input" type="text" name="name" placeholder="Enter name"/>
         </div>
<div className="label">Email address</div>
<div className="control">
 <input type="email" className="input" name="email" placeholder="Enter email" />
<p className="help">
  We'll never share your email with anyone else.
</p>
</div>

<div className="field">
   <div className="label">Message</div>
<div className="control">
  <textarea className="textarea" rows='5' name="message" placeholder="Enter message"/>
  </div> 

</div>
<br/>
<button class="button is-primary" type="submit">
Submit
</button>
{serverState.status && (
            <p className={!serverState.status.ok ? "errorMsg" : ""}>
            {serverState.status.msg}</p>
        )}
</form>
  );
};

export { Contactform }
